"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Info, Users, Award, BarChart2 } from "lucide-react"
import { useContract } from "@/context/contract-context"
import { formatAddress, getVotingTypeText, getRestrictionText, getResultTypeText } from "@/lib/utils"
import { toast } from "sonner"

export default function OwnerDashboard({
  campaignInfo,
  proposals,
  candidates,
  isBeforeStart,
  isDuringVoting,
  isAfterEnd,
  campaignId,
  onUpdate,
}) {
  const { contract } = useContract()
  const [isLoading, setIsLoading] = useState(false)

  // Function to update campaign status if needed
  const updateCampaignStatus = async () => {
    if (!contract) return

    try {
      setIsLoading(true)

      // Call the appropriate contract function to update status
      // Note: This is a placeholder - you'll need to implement the actual contract function
      // const tx = await contract.updateCampaignStatus(campaignId)

      toast.promise(
        Promise.resolve(), // Replace with actual transaction
        {
          loading: "Updating campaign status...",
          success: "Campaign status updated successfully!",
          error: "Failed to update campaign status",
        },
      )

      // Wait for transaction to complete
      // await tx.wait()

      // Refresh campaign data
      onUpdate()
    } catch (error) {
      console.error("Error updating campaign status:", error)
      toast.error(error.message || "Failed to update campaign status")
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="space-y-6">
      <Card className="border-primary/20 bg-primary-foreground/5">
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle className="text-xl font-bold">Campaign Owner Dashboard</CardTitle>
            <Badge variant="outline" className="border-primary/30 text-primary">
              Owner
            </Badge>
          </div>
          <CardDescription>You created this campaign. Monitor its progress and manage settings here.</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="bg-background/60 p-4 rounded-lg flex flex-col items-center justify-center text-center">
              <Users className="h-8 w-8 text-primary mb-2" />
              <h3 className="text-lg font-medium">Registered Voters</h3>
              <p className="text-3xl font-bold">{campaignInfo.registeredVoterCount}</p>
            </div>

            <div className="bg-background/60 p-4 rounded-lg flex flex-col items-center justify-center text-center">
              <Award className="h-8 w-8 text-primary mb-2" />
              <h3 className="text-lg font-medium">Total Votes</h3>
              <p className="text-3xl font-bold">{campaignInfo.totalVotes}</p>
            </div>

            <div className="bg-background/60 p-4 rounded-lg flex flex-col items-center justify-center text-center">
              <BarChart2 className="h-8 w-8 text-primary mb-2" />
              <h3 className="text-lg font-medium">{campaignInfo.votingType === 0 ? "Candidates" : "Proposals"}</h3>
              <p className="text-3xl font-bold">{campaignInfo.itemCount}</p>
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-4">
            <div className="bg-background/60 p-4 rounded-lg">
              <h3 className="font-medium mb-2">Campaign Configuration</h3>
              <ul className="space-y-1 text-sm">
                <li className="flex justify-between">
                  <span className="text-muted-foreground">Type:</span>
                  <span>{getVotingTypeText(campaignInfo.votingType)}</span>
                </li>
                <li className="flex justify-between">
                  <span className="text-muted-foreground">Restriction:</span>
                  <span>{getRestrictionText(campaignInfo.restriction)}</span>
                </li>
                <li className="flex justify-between">
                  <span className="text-muted-foreground">Results:</span>
                  <span>{getResultTypeText(campaignInfo.resultType)}</span>
                </li>
              </ul>
            </div>

            <div className="bg-background/60 p-4 rounded-lg">
              <h3 className="font-medium mb-2">Campaign Status</h3>
              <div className="space-y-2">
                {isBeforeStart && (
                  <p className="text-sm">
                    Campaign is in registration phase. Voting will begin at the scheduled start time.
                  </p>
                )}

                {isDuringVoting && (
                  <p className="text-sm">
                    Voting is currently active. Results will be available when the campaign ends.
                  </p>
                )}

                {isAfterEnd && <p className="text-sm">Campaign has ended. Final results are now available.</p>}
              </div>
            </div>
          </div>
        </CardContent>
        <CardFooter>
          {isBeforeStart && (
            <Alert className="w-full border-amber-200 bg-amber-50 dark:bg-amber-950/20 dark:border-amber-900">
              <Info className="h-4 w-4 text-amber-600 dark:text-amber-400" />
              <AlertDescription className="text-amber-800 dark:text-amber-300 ml-2 text-sm">
                The campaign is in the registration phase. Participants can register until voting begins.
              </AlertDescription>
            </Alert>
          )}

          {isDuringVoting && (
            <Alert className="w-full border-green-200 bg-green-50 dark:bg-green-950/20 dark:border-green-900">
              <Info className="h-4 w-4 text-green-600 dark:text-green-400" />
              <AlertDescription className="text-green-800 dark:text-green-300 ml-2 text-sm">
                Voting is active. Participants can cast their votes until the end time.
              </AlertDescription>
            </Alert>
          )}

          {isAfterEnd && (
            <Alert className="w-full border-blue-200 bg-blue-50 dark:bg-blue-950/20 dark:border-blue-900">
              <Info className="h-4 w-4 text-blue-600 dark:text-blue-400" />
              <AlertDescription className="text-blue-800 dark:text-blue-300 ml-2 text-sm">
                The campaign has ended. Results are now finalized and available for viewing.
              </AlertDescription>
            </Alert>
          )}
        </CardFooter>
      </Card>

      {/* Live Results Section for Owner */}
      {isDuringVoting && (
        <Card>
          <CardHeader>
            <CardTitle>Live Results (Owner View)</CardTitle>
            <CardDescription>
              As the campaign owner, you can see live results while voting is in progress.
            </CardDescription>
          </CardHeader>
          <CardContent>
            {campaignInfo.votingType === 1 /* ProposalBased */ ? (
              <div className="space-y-4">
                {proposals.map((proposal) => (
                  <div key={proposal.id} className="bg-background/60 p-4 rounded-lg">
                    <div className="flex justify-between items-center mb-2">
                      <h3 className="font-medium">Proposal {proposal.id + 1}</h3>
                      <Badge variant="secondary">{proposal.voteCount} votes</Badge>
                    </div>
                    <p className="text-sm text-muted-foreground">{proposal.content}</p>
                    <div className="mt-2 w-full bg-muted rounded-full h-2.5">
                      <div
                        className="bg-primary h-2.5 rounded-full"
                        style={{
                          width: `${
                            campaignInfo.totalVotes > 0 ? (proposal.voteCount / campaignInfo.totalVotes) * 100 : 0
                          }%`,
                        }}
                      ></div>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="space-y-4">
                {candidates.map((candidate) => (
                  <div key={candidate.id} className="bg-background/60 p-4 rounded-lg">
                    <div className="flex justify-between items-center mb-2">
                      <h3 className="font-medium">{candidate.name}</h3>
                      <Badge variant="secondary">{candidate.voteCount} votes</Badge>
                    </div>
                    <p className="text-xs text-muted-foreground">{formatAddress(candidate.address)}</p>
                    <div className="mt-2 w-full bg-muted rounded-full h-2.5">
                      <div
                        className="bg-primary h-2.5 rounded-full"
                        style={{
                          width: `${
                            campaignInfo.totalVotes > 0 ? (candidate.voteCount / campaignInfo.totalVotes) * 100 : 0
                          }%`,
                        }}
                      ></div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>
      )}
    </div>
  )
}

